function launchTable(table) {
   
    const headings = Array.from(
            table.querySelectorAll(`thead th`)
        ),
        bodyRow = Array.from(
            table.querySelectorAll(`tbody tr`)
        );
    
    // cycle through body cells and add column heading to cell as data label attribute
    for (let i = 0; i < bodyRow.length; i++) {
        let tableCells = bodyRow[i].querySelectorAll(`td`);

        for (let i = 0; i < tableCells.length; i++) {
            if(i === 0 ){
                let thRow = document.createElement('th');
                thRow.innerHTML = tableCells[i].innerHTML;
                thRow.setAttribute('scope', 'row');
                tableCells[i].replaceWith(thRow);
            }

            else if(i > 0 ){
                tableCells[i].setAttribute('data-label', headings[i].innerText);
            }
        }
    }
}

export default {
    launchFn: launchTable,
    launchQuery: 'table',
};