function launchRibbonNotice(el) {
    const closeButton = el.querySelectorAll('.global-header__ribbon__close')[0];

    closeButton.addEventListener('click', function () {
        el.classList.add('global-header__ribbon__animation-close');

        setTimeout(() => {
            el.remove();
          }, 600);
    })
}

const className = 'global-header__ribbon';

export default {
    launchFn: launchRibbonNotice,
    launchQuery: `.${className}`,
};
